import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Game from './game';
import DealCard from './deal-card';
import { useParams } from 'react-router-dom';
import { useEffect, useState } from 'react';
import DealsContainer from './deals-container';
import Description from './description-card';
import GameShow from './slideshow';
import CarouselFade from '../components/carousel';
import { Card } from 'react-bootstrap';
import { Navigate } from "react-router-dom";
import useToken from '../signup/usetoken';
import SideBar from '../components/sidebar';
import Footer from '../components/footer';
function InfoPage() {

    const [mobile,setMobile] = useState(window.innerWidth < 991 ? true : false)
    window.addEventListener("resize",() =>{
        if(window.innerWidth < 991){
            setMobile(true);
            // window.location.reload()
        }
        else{
            setMobile(false)
        }

    })
    const { id } = useParams();
    const [gameInfo,setGameInfo] = useState({})
    const [isBusy,setIsBusy] = useState(true);
    useEffect(()=>{
        fetch("http://localhost:4000/info/" + id)
        .then((response) => response.json())
        .then(data => {
            
            setGameInfo(data)
            
        })
        .then(() =>{
            setIsBusy(false)
        })
    },[])

   

    if(isBusy === false && gameInfo.deals){
        return (
            <>
            {!mobile && 
            <Col sm={12} md={4} lg={3}>
                <SideBar />
                <Footer />
            </Col>
            }
            <Col>
                    <Container className="info-container scroll-bar">
                        <Row>
                            <Game gameInfo={gameInfo} />
                        </Row>
                        {gameInfo.gameDescription && <><p className='sections-heading text-center p-0 m-0'>Game Description</p><Row>
                            <Description gameDescription={gameInfo.gameDescription} />
                        </Row></>}
                        {gameInfo.screenShots.length > 0 &&
                            <><p className='sections-heading text-center p-0 m-0'>Game Show</p><Row>
                                <CarouselFade images={gameInfo.screenShots} />
                            </Row></>}
                        <p className='sections-heading text-center p-0 m-0'>Best Deals</p>
                        <Row>

                            <DealsContainer gameInfo={gameInfo} />
                            
                        </Row>
                        {mobile && <Footer/>}
                    </Container>
                    
                    
            </Col></>
          );
    }

}

export default InfoPage;