import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Game from './game';
import DealCard from './deal-card';
import React from 'react';
import { Card } from 'react-bootstrap';

function DealsContainer(props){
    return(
    <Container className='glass deals-container h-100 scroll-bar'>
        <Card.Title>Deals</Card.Title>
        {props.gameInfo.deals.map(deal => {
            if (deal.price < props.gameInfo.normalPrice) {
                return (
                    <DealCard key={deal._id} dealInfo={deal} />
                );
            }

        })}
 
    </Container>
    )
}

export default DealsContainer