import React from "react";
import { Col, Container, Row } from "react-bootstrap";
import CarouselFade from "../components/carousel";
import SmallCard from "../components/small-card";
import { useState,useEffect} from "react";
import {redirect, useNavigate, useSearchParams} from "react-router-dom"
import SideBar from "../components/sidebar";
import Footer from "../components/footer";
import { Navigate } from "react-router-dom";


function Content({token}){

    const [mobile,setMobile] = useState(window.innerWidth < 991 ? true : false)
    window.addEventListener("resize",() =>{
        if(window.innerWidth < 767){
            setMobile(true);
            // window.location.reload()
        }
        else{
            setMobile(false)
        }

    })
    let [sortParams, setSortParams] = useSearchParams()
    const filter = sortParams.get("sort")

    const [data,setData] = useState([]);
    const [covers,setCovers] = useState([])
    useEffect(() => {
        fetch("http://localhost:4000/?" + new URLSearchParams({
            sort:filter
        }))
            .then((result) => result.json())
            .then(data => setData(data)) 
                
           
            
            fetch("http://localhost:4000/covers")
            .then(array => array.json())
            .then(images => setCovers(images));

    }, [])
    return(
        
        <>
        <Col sm={12} md={4} lg={3}>
            <SideBar />
            { !mobile && <Footer />}
        </Col>
        <Col lg={9} md={8} sm={12}>
            <Container className="content overflow-auto scroll-bar p-0">
                <Row className="g-4">
                    <Col >
                        <CarouselFade images={covers} />
                    </Col>
                </Row>
                <Row className="cards-container g-4">
                    {data.map((game) => {
                        return (
                            <Col lg={3} md={6} sm={12} className="card-container" key={game._id}>
                                <SmallCard
                                    game={game} /></Col>
                        );
                    })}
                    {mobile && <Footer/>}
                </Row>

                
            </Container>
            
        </Col>
        </>
            
        


    )
}

export default Content