import { Link } from '@mui/material';
import Container from 'react-bootstrap/Container';
import { useEffect, useState } from 'react';
import { Col, Row } from 'react-bootstrap';
import Card from 'react-bootstrap/Card';

function Game(props) {
  
  const [bestDeal,setbestDeal] = useState({});
  const min = Math.min(...props.gameInfo.deals.map(deal => deal.price));
  useEffect(()=>{
    props.gameInfo.deals.filter(deal => {
      if (deal.price === min)
        setbestDeal(deal)
    });
  },[])
  function toDateTime(secs) {
    if (secs === 0 ){
        return "No Detail"
    }
    var t = new Date(secs * 1000); // Epoch

    return t.toDateString().split(" ").slice(1).toString();
}
  function handleRedirect(){
    window.location.href = "https://www.cheapshark.com/redirect?dealID=" + bestDeal.dealID;
  }
  return (
    <Card  className = "glass ">
      <Row className="details-container" lg={12} sm={12}>
        <Col lg={4} md={12}>
          <Card.Img className="card-img-left" src={props.gameInfo.thumb} />
        </Col>
        <Col lg={8} md={12} className='card-body-right'>
          {/* <Card.Body > */}
          
            
            {/* <Card.Text>
              {props.gameInfo.gameDescription != null ? props.gameInfo.gameDescription : "No Description Available"}
            </Card.Text> */}
              <Container className="info-text">
                <Card.Title>{props.gameInfo.title}</Card.Title>
                <Card.Text style={{"color":"white"}}>Details:</Card.Text>
                {props.gameInfo.publishers.length > 0 &&  <Card.Text>Publisher: {props.gameInfo.publishers.toString()}</Card.Text>}
                {props.gameInfo.releaseDate  > 0 &&  <Card.Text>Released: {toDateTime(props.gameInfo.releaseDate)}</Card.Text>}
                {props.gameInfo.ageLimit > 0 &&  <Card.Text> AgeLimit: {"+" + props.gameInfo.ageLimit}</Card.Text>}
                {props.gameInfo.steamRatingText != null &&  <Card.Text> Reviews: {props.gameInfo.steamRatingText}</Card.Text>}
                {props.gameInfo.steamRatingCount > 0 &&  <Card.Text> Recommendations: {props.gameInfo.steamRatingCount}</Card.Text>}
              </Container>
              <Container className = "smallcard-footer">
                <p className='price'>{"$" + props.gameInfo.normalPrice}</p>
                <button onClick={handleRedirect} className="custom-btn" >{bestDeal.price === 0 ? "Free" : "$" +  bestDeal.price}</button>
              </Container>
              
              
              
           
            
          {/* </Card.Body> */}
        </Col>
        </Row>

    </Card>
  );
}

export default Game;
