import Card from 'react-bootstrap/Card';

function DealCard(props) {
  function handleRedirect(){
    window.location.href = "https://www.cheapshark.com/redirect?dealID=" + props.dealInfo.dealID;
  }
  return (
    <Card className="deal-card" >
      <Card.Body className="deal-card-body" >
          <div className='store-info'>
            <img className="store-icon" src={"https://www.cheapshark.com/" + props.dealInfo.storeInfo.icon}></img>
            <p>{props.dealInfo.storeInfo.storeName}</p>
          </div>

            <button className="custom-btn" onClick={handleRedirect}>{props.dealInfo.price === 0 ? "Free" : "$" + props.dealInfo.price}</button>
      </Card.Body>
    </Card>
  );
}

export default DealCard;