import React, { useState } from "react";
import Card from 'react-bootstrap/Card';
import { Col, Container, Row } from "react-bootstrap";
import { Link } from "react-router-dom";
import FavoriteBorderIcon from "@mui/icons-material/FavoriteBorder";


function SmallCard(props){
    function handleRedirect(){
        
        window.location.href = "https://www.cheapshark.com/redirect?dealID=" + props.game.dealID;
      }
    return (
        
            <Card className="glass small-card ">
                <Card.Img className="card-img" variant="top" src={props.game.thumb} />
                <Card.Body>
                <Card.Title> <Link className="card-title" to={"/info/" + props.game.gameID}>{props.game.title.length > 20 ? props.game.title.slice(0,17) + "...": props.game.title }</Link> </Card.Title>
                <Card.Text style={{display:"flex",justifyContent:"space-between"}}>
                    {props.game.storeName}
                    <FavoriteBorderIcon className="add-wishlist"/>
                </Card.Text>
                <Container  className="smallcard-footer">
                    <p className="price">{props.game.normalPrice}</p>
                    <button className="custom-btn" onClick={handleRedirect} >{props.game.salePrice === 0 ? "Free" : "$" + props.game.salePrice}</button>
                </Container>
                
                </Card.Body>
            </Card>

    )
}


export default SmallCard